import React, {ChangeEvent, useEffect, useState} from "react";
import {TextField} from "@material-ui/core";
import {validate} from "./validate";
import makeStyles from "@material-ui/core/styles/makeStyles";

type inputType = {
    id?: string
    className?:string
    label:string
    title?:string
    variant?:string
    helperText?:string
    value:string
    addInputValue: (value: string) => void
    type?: string
    required?:boolean
    multiline?:any
    rows?:number
    // name: string
    placeholder: string
    validate: (value: string, min: number, max: number, name: string) => string
}
export const Input = (props: inputType) => {
    const useStyles = makeStyles((theme) => ({
        root: {
            display: 'flex',
            flexWrap: 'wrap',
        },
        margin: {
            margin: theme.spacing(1),
        },
        input: {
            display: 'none',
        },
    }));
    const classes = useStyles();


    let [value, setValue] = useState<string|number>('');
    let [error, setError] = useState<string>('')
    useEffect(() => {
        setValue(props.value)
    }, [props.value])
    const onChangeValue = (e: ChangeEvent<HTMLInputElement>) => {
        setError('')
        setValue(e.currentTarget.value)
    }
    const onValidate = () => {
        if (props.label==='name') {
            const err = validate(value, 2, 12, 'name')
            err? setError(err): props.addInputValue(value.toString())
        }
        if (props.label==="price") {

            const err = validate(value, 1, 4, 'price')
            err? setError(err): props.addInputValue(value.toString())
        }
        if (props.label==='description') {
            const err = validate(value, 7, 400, 'description')
            err? setError(err): props.addInputValue(value.toString())
        }
        if (props.label==='image') {
            const err = validate(value, 1, 14444, 'image')
            err? setError(err): props.addInputValue(value.toString())
        }
    }
    const handleBlur = () => {
        onValidate()
    }
    const onKeyPressCallback = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === "Enter") {
            onValidate()
        }
    }
    return (

            <TextField type={props.type} value={value} onChange={onChangeValue} helperText={error}
                       id={props.id} name={props.placeholder} placeholder={props.placeholder}
                       className={props.className} label={props.label} onBlur={handleBlur}
                       onKeyPress={onKeyPressCallback}  error={!!error} variant="outlined"
                       rows={props.rows}
            />

    )
}
